export const getParent = async (item, recursive = true) => {
  const parents = []

  if (recursive) {
    while (Number(item.parentId) > 0) {
      const result = await browser.bookmarks.get(item.parentId)
      item = result[0]
      parents.push(item)
    }
  } else {
    const result = await browser.bookmarks.get(item.parentId)
    item = result[0]
    parents.push(item)
  }

  return parents.reverse()
}

const getBookmarkParent = async (item) => {
  const parents = await getParent(item, false)
  return Promise.resolve({
    ...item,
    parents
  })
}

export const getParents = async (bookmarks) => {
  return Promise.all(
    bookmarks.map((item) => {
      return getBookmarkParent(item)
    })
  )
}
