<template>
  <div class="Bookmarks" :class="{ 'Bookmarks--isList': asList }">
    <BrowserBookmark
      v-for="(bookmark, index) in bookmarks"
      :key="bookmark.id"
      :as-list="asList"
      :bookmark="bookmark"
      :is-selected="allowKeyboard && index == selectedItem"
      @folderSelected="(id) => $emit('folderSelected', id)"
    />
  </div>
</template>

<script>
import BrowserBookmark from '@/components/BrowserBookmark.vue'
export default {
  components: {
    BrowserBookmark
  },
  props: {
    bookmarks: {
      type: Array,
      default: () => []
    },
    asList: {
      type: Boolean,
      default: true
    },
    allowKeyboard: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      selectedItem: -1
    }
  },
  created() {
    window.addEventListener('keydown', this.keydownHandler)
  },
  unmounted() {
    window.removeEventListener('keydown', this.keydownHandler)
  },
  methods: {
    scrollItemIntoView() {
      setTimeout(() => {
        const el = document.getElementsByClassName('Bookmark--isSelected')[0]
        if (el) {
          el.scrollIntoViewIfNeeded(false)
          el.focus()
        }
      })
    },
    nextItem(offset = 1) {
      this.selectedItem = Math.min(this.bookmarks.length - 1, this.selectedItem + offset)
      this.scrollItemIntoView()
    },
    prevItem(offset = 1) {
      if (this.selectedItem === 0) {
        this.selectedItem = -1
        this.$emit('navigateTop')
        return
      }
      this.selectedItem = Math.max(0, this.selectedItem - offset)
      this.scrollItemIntoView()
    },
    keydownHandler(event) {
      if (!this.allowKeyboard || event.metaKey) {
        return true
      }

      if (event.target.tagName === 'INPUT' && (event.keyCode === 37 || event.keyCode === 39)) {
        return true
      }

      const verticalOffset = this.selectedItem === -1 ? 1 : this.asList ? 1 : 5

      if (event.keyCode === 40) {
        event.preventDefault()
        this.nextItem(verticalOffset)
      } else if (event.keyCode === 39) {
        // event.preventDefault()
        this.nextItem()
      } else if (event.keyCode === 38) {
        event.preventDefault()
        this.prevItem(verticalOffset)
      } else if (event.keyCode === 37) {
        // event.preventDefault()
        this.prevItem()
      } else if (event.keyCode === 13) {
        if (this.bookmarks[this.selectedItem] && !this.bookmarks[this.selectedItem].url) {
          this.$emit('folderSelected', this.bookmarks[this.selectedItem].id)
        }
      }
    }
  }
}
</script>

<style lang="postcss" scoped>
.Bookmarks {
  width: 100%;
  display: flex;
  flex-wrap: wrap;

  &--isList {
    flex-direction: column;
  }
}
</style>
